import React, { useState, Fragment } from 'react'
import { Form } from 'react-bootstrap'
import { Text } from '../FormGroup/styles'
import { Option } from './Option'
import PropTypes from 'prop-types'

export const FormSelect = ({
  name,
  text,
  handleOnChange,
  options,
  multiple,
}) => {
  const [selected, setSelected] = useState([])

  const handleOnSelect = (event) => {
    if (selected.includes(event.target.value))
      setSelected([...selected.filter((ele) => ele !== event.target.value)])
    else setSelected([...selected, event.target.value])
    handleOnChange({ target: { name, value: selected } })
  }

  return (
    <Fragment>
      <Form.Control as="select" name={name} multiple={multiple}>
        {options.map((option, index) => (
          <Option
            key={index}
            isMultiple={multiple}
            option={option}
            handleOnClick={handleOnSelect}
          />
        ))}
      </Form.Control>
      <Text className="text-muted">{text}</Text>
    </Fragment>
  )
}

FormSelect.prototype = {
  name: PropTypes.string,
  text: PropTypes.string,
  handleOnChange: PropTypes.func,
  options: PropTypes.array,
  multiple: PropTypes.bool,
}
