import styled, { css } from 'styled-components'
import { Form, Table as TableBoot } from 'react-bootstrap'

export const Formulario = styled(Form)`
  &&& {
    margin: 20px;
    border: 1px solid rgba(219, 219, 219, 0.5);
    box-shadow: 0px 0px 5px rgba(219, 219, 219, 0.5);
  }
`
export const Table = styled(TableBoot)`
  @media screen and (max-width: 500px) {
    display: none;
  }
`

export const Img = styled.img`
  display: none;
  ${(props) =>
    props.isValid &&
    css`
      display: initial;
      margin: 0 auto;
      right: 0;
      left: 0;
    `}
`
