import React, { Fragment } from 'react'
import { Form, ProgressBar } from 'react-bootstrap'
import { useAddImagesHoc } from '../../../hoc/useAddImagesHoc'
import PropTypes from 'prop-types'
import { Img } from '../styles'

export const FormFile = ({ name, onChange, img }) => {
  const [
    text,
    porcentValue,
    isValid,
    imgRef,
    handlerOnChange,
    handleIsValid,
  ] = useAddImagesHoc(onChange, name)

  return img ? (
    <img src={img} width="100%" height="100%" isValid={isValid} />
  ) : (
    <Fragment>
      <Form.File id="formcheck-api-custom" custom>
        <Form.File.Input
          isValid={isValid}
          accept="image/*"
          onChange={handlerOnChange}
          onLoad={handleIsValid}
        />
        <Form.File.Label data-browse="Seleccionar archivo">
          {text}
        </Form.File.Label>
      </Form.File>
      <ProgressBar
        animated={porcentValue !== 100}
        variant={porcentValue === 100 ? 'success' : 'info'}
        now={porcentValue}
        label={porcentValue}
      />
      <Img ref={imgRef} width="50px" height="50px" isValid={isValid} />
    </Fragment>
  )
}

FormFile.prototype = {
  name: PropTypes.string,
  onChange: PropTypes.func,
  img: PropTypes.string,
}
