import styled, { css } from 'styled-components'
import { Toast as ToastBoot } from 'react-bootstrap'

export const ToastContainer = styled.div`
  position: fixed;
  min-height: 100px;
  bottom: 0;
  right: 0;
  padding: 20px;
`

export const ToastCard = styled(ToastBoot)`
  ${(props) =>
    props.isSuccessful
      ? css`
          border: 2px solid var(--success);
        `
      : css`
          border: 2px solid var(--danger);
        `};
`
export const Header = styled(ToastBoot.Header)``
export const Body = styled(ToastBoot.Body)``
