import React from 'react'
import PropType from 'prop-types'
import { Modal as Mod, Button } from 'react-bootstrap'

export const Modal = ({ show, handleToggleShow, children }) => {
  return (
    <Mod show={show} onHide={handleToggleShow}>
      <Mod.Header closeButton>
        <Mod.Title>Imagen</Mod.Title>
      </Mod.Header>
      <Mod.Body>{children}</Mod.Body>
      <Mod.Footer>
        <Button variant="primary" onClick={handleToggleShow}>
          Cerrar
        </Button>
      </Mod.Footer>
    </Mod>
  )
}

Modal.prototype = {
  show: PropType.bool,
  handleToggleShow: PropType.func,
  children: PropType.node,
}
