import { useHandleOnChangeForm } from './useHandleOnChangeForm'

const { useState } = require('react')

export function useHandleAddProducts() {
  const [formDataProduct, handleOnChangeProduct] = useHandleOnChangeForm()
  const [formDataProducts, setFormDataProducts] = useState([])
  const [isSuccessful, setIsSuccessful] = useState(false)
  const [show, setShow] = useState(false)
  const handleShow = () => setShow(!show)
  const handleAddProductsCSV = ({ data, isSuccessful }) => {
    if (isSuccessful) {
      setFormDataProducts(data)
      setShow(true)
      setIsSuccessful(isSuccessful)
    } else {
      setShow(true)
      setIsSuccessful(isSuccessful)
    }
  }

  const handleAddProduct = () => {
    const prod = formDataProduct

    setFormDataProducts([...formDataProducts, prod])
    setShow(true)
    setIsSuccessful(true)
    handleOnChangeProduct({ resetear: true })
  }

  const handleOnChangeImage = (products) => setFormDataProducts(products)

  const handleNewCandidate = () => {
    setFormDataProducts([])
  }

  return [
    isSuccessful,
    formDataProducts,
    formDataProduct,
    handleAddProductsCSV,
    handleAddProduct,
    handleShow,
    handleOnChangeImage,
    handleOnChangeProduct,
    handleNewCandidate,
  ]
}
