import React, { Fragment, useState } from 'react'
import { Form } from '../components/Form'
import { Toast } from '../components/Toast'
import { Button } from '../components/Button'
import { Layout } from '../components/Layout'
import formSchema from '../components/schemas/FormSchema'
import { TableOfCSV } from '../components/Form/TableOfCSV'
import { CsvReader } from '../components/Form/FormGroupCsv'
import { useHandleAddProducts } from '../hooks/useHandleAddProducts'
import { useHandleOnChangeForm } from '../hooks/useHandleOnChangeForm'
import formSchemaProduct from '../components/schemas/FormSchemaProduct'
import { useRegisterCandiadtoHoc } from '../hoc/useRegisterCandidatoHoc'

const Registro = () => {
  // const [partOfForm, setPartOfForm] = useState(0)
  const [formData, handleOnChange] = useHandleOnChangeForm()
  const [
    isSuccessful,
    formDataProducts,
    formDataProduct,
    handleAddProductsCSV,
    handleAddProduct,
    handleShow,
    handleOnChangeImage,
    handleOnChangeProduct,
    handleNewCandidate,
  ] = useHandleAddProducts()
  const [
    show,
    message,
    partOfForm,
    handleSubmit,
    handleChangePartOfForm,
  ] = useRegisterCandiadtoHoc(
    formData,
    formDataProducts,
    handleNewCandidate,
    handleOnChange
  )

  return (
    <Layout>
      {partOfForm === 0 ? (
        <Fragment>
          <Form
            formSchema={formSchema}
            formData={formData}
            handleOnChange={handleOnChange}
          />
          <Button
            text="Agregar productos manualmente"
            onClick={() => handleChangePartOfForm(1)}
          />
          <Button
            text="Agregar productos con un archivo CSV"
            onClick={() => handleChangePartOfForm(2)}
          />
        </Fragment>
      ) : partOfForm === 1 ? (
        <Fragment>
          <Form
            formSchema={formSchemaProduct}
            formData={formDataProduct}
            handleOnChange={handleOnChangeProduct}
          />
          <Button text="Agregar Nuevo Producto" onClick={handleAddProduct} />
          {formDataProducts.length > 0 && formData && (
            <Button text="Registrar" onClick={handleSubmit} />
          )}
          {formDataProducts && (
            <TableOfCSV
              data={formDataProducts}
              headers={formSchemaProduct}
              handlerOnChangeImage={handleOnChangeImage}
            />
          )}
        </Fragment>
      ) : (
        <Fragment>
          <CsvReader onLoad={handleAddProductsCSV} />

          {formDataProducts.length > 0 && formData && (
            <Button text="Registrar" onClick={handleSubmit} />
          )}
          {formDataProducts && (
            <TableOfCSV
              data={formDataProducts}
              headers={formSchemaProduct}
              handlerOnChangeImage={handleOnChangeImage}
            />
          )}
        </Fragment>
      )}
      <Toast
        show={show}
        title="Productos"
        timer="Ahora"
        body={message}
        onClose={handleShow}
        isSuccessful={isSuccessful}
      />
    </Layout>
  )
}

export default Registro
