import React, { Fragment } from 'react'
import { Btn, Spinn } from './styles'
import PropTypes from 'prop-types'

export const Button = ({
  text,
  size,
  variant,
  isActive,
  isLoading,
  onClick,
}) => (
  <Btn
    variant={variant || 'primary'}
    size={size}
    disabled={isActive || (isLoading && true)}
    onClick={onClick}
  >
    {isLoading ? (
      <Fragment>
        <Spinn
          as="span"
          animation="grow"
          size="sm"
          role="status"
          aria-hidden="true"
        />
        Cargando...
      </Fragment>
    ) : (
      text
    )}
  </Btn>
)

Button.prototype = {
  text: PropTypes.string,
  size: PropTypes.string,
  variant: PropTypes.string,
  isActive: PropTypes.bool,
  onClick: PropTypes.func,
}
