import { useState, useRef } from 'react'
import { storage } from '../firebase/config'
import { v4 as uuid } from 'uuid'

export function useAddImagesHoc(onChange, name) {
  const imgRef = useRef(null)
  const [text, steText] = useState('Arrastra el archivo')
  const [porcentValue, setPorcentValue] = useState(0)
  const [isValid, setIsValid] = useState(false)

  const handleIsValid = () => {
    setIsValid(true)
  }

  const handlerOnChange = (event) => {
    if (event.target.files[0]) {
      const file = event.target.files[0]
      setIsValid(true)
      steText('Listo!')

      const imgUrl = URL.createObjectURL(event.target.files[0])
      imgRef.current.src = imgUrl
      const ext = file.name.split('.')
      
      const fileUpload = storage.ref(
        'prueba/' + (uuid() + '.' + ext[ext.length - 1])
      )
      const fileReady = fileUpload.put(file)

      fileReady.on(
        'state_changed',
        (snap) => {
          const percentage = (snap.bytesTransferred / snap.totalBytes) * 100
          setPorcentValue(percentage)
        },
        function err(err) {},
        function complete() {
          fileUpload
            .getDownloadURL()
            .then((res) => onChange({ target: { name, value: res } }))
        }
      )
    }
  }
  return [text, porcentValue, isValid, imgRef, handlerOnChange, handleIsValid]
}
