import { database } from '../firebase/config'
import { useState } from 'react'

export function useRegisterCandiadtoHoc(
  formData,
  formDataProducts,
  handleNewCandidate,
  handleOnChange
) {
  const [message, setMessage] = useState('Se agrego producto')
  const [partOfForm, setPartOfForm] = useState(0)
  const [show, setShow] = useState(false)

  const handleSubmit = () => {
    const candidato = {
      ...formData,
      productos: [...formDataProducts],
    }
    const candidatos = database.ref('candidatos')
    candidatos.push(candidato, (err) => {
      if (err) throw err
      setMessage('Se registro correctamente')
      setShow(true)
    })
    handleNewCandidate()
    handleOnChange({ resetear: true })
    setPartOfForm(0)
  }
  const handleChangePartOfForm = (part) => {
    setPartOfForm(part)
  }

  return [show, message, partOfForm, handleSubmit, handleChangePartOfForm]
}
