import styled, { css } from 'styled-components'

export const Option = styled.option`
  transition: all 0.3s;
  ${(props) =>
    props.isMultiple &&
    css`
      border: 1px solid var(--primary);
      margin: 4px;
      border-radius: 5px;
      background-color: var(--primary);
    `}

  ${(props) =>
    props.isSelected &&
    css`
      border: none;
      color: white;
      background-color: var(--secundary);
    `};
`
