import React from 'react'
import { Table } from '../styles'
import { FormFile } from '../FormFile'
import { Modal } from '../../Modal'
import { useState } from 'react'
import { Button } from 'react-bootstrap'
import PropTypes from 'prop-types'

export const TableOfCSV = ({ data, headers, handlerOnChangeImage }) => {
  const [content, setContent] = useState({ name: '', fila: {} })
  const [show, setShow] = useState(false)

  const handleOnChange = (event) => {
    const newFila = content.fila
    console.log(newFila)
    newFila.imagen = event.target.value
    console.log(newFila)
    data[content.index] = newFila
    handlerOnChangeImage([...data])
  }

  const ModalForm = ({ name, fila }) => (
    <Modal show={show} handleToggleShow={() => setShow(!show)}>
      <FormFile name={name} onChange={handleOnChange} img={fila[name]} />
    </Modal>
  )

  return (
    <Table striped bordered hover>
      <thead>
        <tr>
          <th>Nro.</th>
          {headers.map((cabecera, index) => (
            <th key={index}>{cabecera.label}</th>
          ))}
        </tr>
      </thead>
      <tbody>
        {data.map((fila, index) => (
          <tr key={index}>
            <td>{index + 1}</td>
            {headers.map((cabecera, index) => (
              <td key={index}>
                {cabecera.name === 'imagen' ? (
                  <Button
                    onClick={() => {
                      setContent({ name: cabecera.name, fila, index })
                      setShow(true)
                    }}
                  >
                    Acciones
                  </Button>
                ) : (
                  fila[cabecera.name]
                )}
              </td>
            ))}
          </tr>
        ))}
      </tbody>
      <ModalForm {...content} />
    </Table>
  )
}

TableOfCSV.prototype = {
  data: PropTypes.array,
  headers: PropTypes.array,
  handlerOnChangeImage: PropTypes.func,
}
